.modalinvoice {
  font-family: sans-serif;
  border: 1px solid #000;
  padding-bottom: 10px;
}

.modeldivcard {
  width: 60vw;
  transform: translate(-10%);
}

.topdiv {
  height: 13vh;
  position: relative;
}
.topdiv .invoiceorglogodiv{
  width: 20%;
 height: 80%;
 margin: 10px 0px 0px 30px;
}

.topdiv .invoiceorglogodiv .orgpreviewlogo{
  width: 100%;
  height: 100%;
}

.topinnerdiv {
  position: absolute;
  right: 0;
  top: 4%;
  height: 100%;
  width: 50%;

}

.topinnerdiv h5 {
  padding: 0px;
  margin: 0px;
}

.topinnerdiv p {
  margin: -1px 0px 3px 0px;
}

.topbottomdiv {
  height: 15px;
  width: 100%;
}

.invoicediv {
  height: 60px;
  width: 100%;
  background-color: #eee;
  display: flex;
  align-items: center;
  justify-content: center;
}

.inerinvoicediv {
  width: 95%;
  height: 33px;
  display: flex;
  align-items: center;
}

.firstinvoicediv {
  width: 30%;
}


.secondinvoicediv,
.thirdinvoicediv {
  border-right: 2px solid #000;
}

.forthinvoicediv {
  width: 35%;
  text-align: right;
}

.addressmaindiv {
  height: 150px;
  width: 100%;
  display: flex;
  border-bottom: 1px dotted #000;
}

.addressmaindiv .inneraddduiv {
  height: 100%;
  width: 50%;
  padding: 0px 5px 5px 10px;
}

.addressmaindiv .inneraddduiv h5 {
  margin: 7px 0px 0px 0px;
  font-size: 15px;
}

.addressmaindiv .inneraddduiv h6 {
  margin: 20px 0px 0px 0px;
  font-size: 14px;
}

.addressmaindiv .inneraddduiv p {
  margin: 5px 0px 3px 0px;
}

.detail {
  height: 60px;
  padding: 2px 0px 0px 10px;
  /* border-bottom: 1px dotted #000; */
}

.detail .detailtable {
  width: 100%;
}

.detail .detailtable tr td {
  width: 30%;
}

